import { Component, Fragment } from 'react';
import Head from 'next/head';
import GlobalStyle from '../GlobalStyle';
import Slide from '../Slide';
import { Container } from './Deck.styles';

const omit = (obj, arr) =>
  Object.keys(obj)
    .filter(k => !arr.includes(k))
    .reduce((acc, key) => ((acc[key] = obj[key]), acc), {});

const renderHead = head => {
  if (head) {
    return (
      <Head>
        {Object.keys(head).map(key => {
          if (typeof head[key] === 'string') {
            return React.createElement(key, { key }, head[key]);
          }

          return React.createElement(key, { key, ...head[key] }, null);
        })}
      </Head>
    );
  }

  return null;
};

class Deck extends Component {
  constructor(props) {
    super(props);
    this.state = {
      slideIndex: 0,
      scale: 0,
    };

    this.template = props.templates[props.deck.template];
    this.slides = props.deck.slides.map((slide, i) => ({ ...slide, key: i }));
    this.slidesCount = this.slides.length;
    this.head = { ...this.template.head, ...props.deck.head };
  }

  componentDidMount() {
    window.addEventListener('keydown', this.handleArrowPress);
    window.addEventListener('resize', this.handleScaleChange);

    this.handleScaleChange({ currentTarget: window });

    const slideIndex = Number(window.location.hash.replace('#', ''));

    if (this.state.slideIndex !== slideIndex) {
      this.setState({ slideIndex });
    }
  }

  componentDidUpdate(prevProps, prevState) {
    if (prevState.slideIndex !== this.state.slideIndex) {
      window.location.hash = this.state.slideIndex;
    }
  }

  componentWillUnmount() {
    window.removeEventListener('keydown', this.handleArrowPress);
    window.removeEventListener('resize', this.handleScaleChange);
  }

  handleScaleChange = ({ currentTarget: { innerWidth, innerHeight } }) => {
    const { slideWidth, slideHeight } = this.template.defaults;
    const scaleX = innerWidth / slideWidth;
    const scaleY = innerHeight / slideHeight;
    this.setState({ scale: Math.min(scaleX, scaleY) });
  };

  handleArrowPress = ({ keyCode }) => {
    if (keyCode === 37 && this.state.slideIndex !== 0) {
      this.prevSlide();
    } else if (keyCode === 39 && this.state.slideIndex !== this.slidesCount - 1) {
      this.nextSlide();
    }
  };

  nextSlide = () => this.setState(state => ({ slideIndex: state.slideIndex + 1 }));

  prevSlide = () => this.setState(state => ({ slideIndex: state.slideIndex - 1 }));

  render() {
    const { slideIndex, scale } = this.state;
    const { slideWidth, slideHeight } = this.template.defaults;

    return (
      <Fragment>
        {renderHead(this.head)}
        <GlobalStyle templateStyle={this.template.styles} />
        <Container>
          {scale &&
            this.slides.map(slide => {
              const Comp = this.template.slides[slide.template];
              const props = omit(slide, ['template']);

              return (
                <Slide
                  visible={slideIndex === slide.key}
                  key={slide.key}
                  slideWidth={slideWidth}
                  slideHeight={slideHeight}
                  scale={scale}
                >
                  <Comp {...props} style={slide.style} />
                </Slide>
              );
            })}
        </Container>
      </Fragment>
    );
  }
}

export default Deck;
